@import url("https://fonts.googleapis.com/css2?family=Bangers&display=swap");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
  font-family: "Bangers";
  letter-spacing: 1px;
}

.board {
  background-color: #222;
  display: flex;
  flex-direction: column;
  flex-grow: 3;
}
.board__list-item {
  display: flex;
  justify-content: space-evenly;
  flex-grow: 1;
  flex-wrap: wrap;
}
.board__list-item h1 {
  margin: 0;
  width: 100%;
  border-bottom: 5px dashed #F2A007;
  border-top: 5px dashed #F2A007;
  padding: 0.6rem 0;
  background-color: rgba(46, 65, 89, 0.5);
  text-align: center;
  font-size: 3rem;
  color: #F2A007;
}
.board__list-item .interface {
  display: flex;
  justify-content: space-around;
}
.board__item {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 2rem;
  background-color: rgba(46, 65, 89, 0.3);
}
.board__item--small {
  min-width: 250px;
}
.board__item h2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  padding: 0.5rem;
  text-align: center;
  font-size: 3rem;
  text-transform: uppercase;
  color: #222;
  letter-spacing: 5px;
  background-color: #F2A007;
}
.board__item h2 img {
  margin-left: 20px;
}
.board__item .buttons {
  margin-top: 1rem;
}

.buttons {
  display: flex;
}

.button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
  margin-right: 5px;
  border: 3px solid;
  padding: 0.5rem 0.7rem;
  font-size: 10px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  box-shadow: 1px 1px 0px 0px, 2px 2px 0px 0px, 3px 3px 0px 0px, 4px 4px 0px 0px, 5px 5px 0px 0px;
  user-select: none;
  touch-action: manipulation;
}
.button:hover {
  background-color: #8B8ABF;
}

.image {
  width: 150px;
}
.image img {
  max-width: 100%;
}

.panel {
  padding: 1rem;
  background-color: #F2A007;
  font-size: 2.5rem;
  color: #222;
}
.panel__stats {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  padding: 10px 0 20px;
}
.panel__item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5rem;
}
.panel__item span {
  color: #D90404;
}

.service-panel {
  padding: 0.6rem;
  margin-top: 1rem;
  color: #F2A007;
}
.service-panel__item {
  font-size: 1.5rem;
  padding: 0.5rem;
}

.info-panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  color: #F2A007;
}
.info-panel__item--with-icon p {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  line-height: 170%;
}

.button .icon {
  width: 25px;
}

.info-panel .icon {
  width: 30px;
}

.icon {
  width: 40px;
}

.sell-panel__item .icon {
  margin-right: 10px;
  width: 35px;
}

.resources {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 1rem 0;
}
.resources__item {
  padding: 0 3rem;
}
.resources__amount {
  margin-right: 1rem;
  font-size: 5rem;
}

.sell-panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.sell-panel__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}
.sell-panel__item label {
  width: 50%;
  color: #F2A007;
  font-size: 2rem;
}
.sell-panel__item input {
  width: 40%;
  height: 80%;
  background-color: #2E4159;
  color: #F2A007;
  font-weight: 700;
  font-size: 1.7rem;
  padding: 3px;
}
.sell-panel .button {
  margin-top: 3rem;
}

body {
  min-height: 100vh;
  display: grid;
}

.game {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
  min-height: 100%;
}